@import url('https://fonts.googleapis.com/css?family=Noto+Sans+TC|Noto+Serif+TC|ZCOOL+KuaiLe&display=swap');

$break-small:500px;
.playMV{
    width: 100%;
    height: 100%;
    position:relative;
    @media screen and (max-width: $break-small) {
      //box-sizing: content-box;
      height: 950px;
      background-color:black;
    }
    // position: fixed; 
    // z-index: -99; 
    // width: 100%; 
    // height: 100%;
}

.formContent{
  position: absolute;
  padding-top: 15px;
  width:600px;
  height:550px;
  bottom:220px;
  right:30px;

  @media screen and (max-width: $break-small) {
    // z-index:999;
    top:550px;
    right:0;
    width:100%;
    padding-top:0;
    background-color:#000000;
  }
  background-color:rgba(222,184,135, 0.5);
  border-radius: 5px;
  border-bottom: 0.5px solid rgba(225,225,225, 0.5);
  font-family:'Noto Sans HK', sans-serif;  
  font-size:18px;
  color:#000000;
    .description{
      padding:15px 25px 15px 25px;
      word-wrap: break-word;
      text-align: justify;
      text-justify:inter-ideograph;
      @media screen and (max-width: $break-small) {
        padding:10px;
        font-size: 13px;
        color:#ffffff;
      }
    
      span{
        font-size:1.1em;
        font-weight: bold;
      }
    }
  
    label{
    @media screen and (max-width: $break-small) {
      font-size:13px;
      color:#ffffff;
    } 
 }
  
}

.letters{
  position: absolute;
  top:120px;
  right:90px;  
  font-family: 'Noto Sans TC', sans-serif;;
  color:#fff;
  text-shadow: black 0.3em 0.2em 0.6em;
  @media screen and (max-width: $break-small) {
      width:90%;
      top:120px;
      left:10px;
    
  }
  
  p{
     font-size:40px;
     font-weight: bold;   
      @media screen and (max-width: $break-small) {
        font-size:25px;        
      }
    }

  span{
    font-size:23px;
    display:inline;
    padding-left:5px;
    @media screen and (max-width: $break-small) {
      display:inline-block;
      font-size:15px;
    }
  }
  .span2{
    @media screen and (max-width: $break-small) {
      display:inline-block;
    }    
   }
}


.button{
    background-color:#ff0000;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    letter-spacing: 0.5em;
    display: inline-block;
    font-size: 18px;
    font-weight: bolder;
    height:50px;
    width:130px;
    border-radius:8px;
    text-align:center;
    line-height:20px;
    @media screen and (max-width: $break-small) {
      font-size: 15px;
      height:40px;
      width:100px;
      padding: 0;
    }

   
}

.button:hover
{
  background-color:	#ce0000;
  transform: translateY(-2px);
}

.button:active,.button:focus
{
  outline:none;
}

.footer{
  height: 500px;
  background-color:yellow;

}









