$break-small:500px;


.footer{
   margin-left:0px;
}

.content{
   
    height:100px;
    border-top: 1px solid #e0e0eb;
    margin:80px 80px 0px 80px;
    width:80%;
    text-align: center;
    @media screen and (max-width: $break-small) {
        width:100%;
        margin:auto 0;
        margin-right:20px;
        padding-top: 50px;
    }
   
    p{
       display: inline-block;
       font-size: 15px;
       color: #8585ad;
       @media screen and (max-width: $break-small) {
            width:300px;
            text-align:center;
        }



    }

}