$break-small: 650px;
// $break-large: 1200px;

.nav{
    background-color:#ff0000;
    @media screen and (max-width: $break-small) {
        box-sizing: content-box;
    }
}

.navTitle{
    text-align: center;
   
    @media screen and (max-width: $break-small) {
        margin:0 auto;
       
    }
    vertical-align: text-bottom;
    a{
        @extend .textShadow;
        font-size:40px;
        font-weight: bolder;
        color:#ffffff;
        display: inline-block;
        padding-top:15px;
        @media screen and (max-width: $break-small) {
            font-size:25px;
            padding-top:0;
            width:80%;
            text-align: left;
            margin-left:20px;
            margin-top:10px;
        }
        span{
            @media screen and (max-width: $break-small) {
                display: inline-block;
            }
        }
    } 
    a{
        text-decoration: none;
        position: relative;
    }

   img{
    position:absolute;
    right:-90px;
    width:auto;
    height:80px;

    @media only screen and (max-width: $break-small) {
        right:-53px;
        top:15px;
      }
  
   }

}


.textShadow{

    text-shadow:

    -3px -3px 0 #000,

    3px -3px 0 #000,

    -3px 3px 0 #000,

    3px 3px 0 #000,

    2px 2px 2px black;
}

