.modal{
    width:100%;
    height:1000px;
    position: absolute;
    top:0px;
    left:0px;
    background-color: rgba(0,0,0,0.5);
    display:none;
    
    
}

.modalContent {
    background-color: #fefefe;
    margin: auto;
    margin-top:20%;
    padding: 20px;
    padding-top:30px;
    border: 1px solid #888;
    width: 60%;
    height:300px;
    border-radius: 10px;
    text-align: center;
  }


/* The Close Button */
.close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    transform: translateY(-20px);
   
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
} 

.calendar{  
    background:#fff;
}
 



