
$break-small:500px;

.main{
    padding:30px 60px 30px 60px;
    width:100%;
    height:1300px;
    @media screen and (max-width: $break-small) {
        padding: 70px 5px 5px 5px;
        
    }
}

.tabCotent{
    width:70%;
    margin-left:10%;
    text-align:center;
    @media screen and (max-width: $break-small) {
        margin-left:0;
        margin-top:10px;
        margin: 0 auto;  
        padding: 20px;      
    }
}

.tabBankInfo{
    width:70%;
    margin: 0 auto;
    margin-top:80px;
    text-align: left;
    @media screen and (max-width: $break-small) {
        margin-left:0;
        padding: 20px;
        margin: 0 auto;       
        text-align:center; 
    }

    
}

.balanceInfo{

    margin-left:20%;
    padding: 20px; 
    margin-top:5%;
    @media screen and (max-width: $break-small) {
        margin-left:0;
        padding-left: 30px;
    }
    h5,p{
       
        @media screen and (max-width: $break-small) {
            font-size:15px;
         }
    }
    hr{
        visibility:hidden;
        @media screen and (max-width: $break-small) {
            visibility:visible;
            width:80%;
         }
       
    }
}

.iBorder{
    display: inline-block;
  
    
}

.check{
    width:30px;
    display: inline-block;
    color:#7FFF00;
   
}

.li{
    list-style: none;
    height: 40px;
}
.ul{
    margin-top:40px;
}

.GiftName{
    display: inline;
    
    font-weight: bolder;
    padding-left:20px;
   
}

.GiftNameok{
    @extend .GiftName;
    color:#00cc00;
    
}

.thankforName{
   
    margin-left:50px;
}

.show{
    display: inline-block;
}

.hide{
    display: none;
}


.donator{
    font-size: 40px;
}

.progressBar{
    width:500px;
    @media screen and (max-width: $break-small) {
        width:300px;
     }
}

.footer{
    height: 200px;
    border:1px solid black;
    
}


